@-webkit-keyframes contentAppear {
  0% {
    display: none;
    opacity: 0; }
  95% {
    display: none;
    opacity: 0; }
  100% {
    display: block;
    opacity: 1; } }
@keyframes contentAppear {
  0% {
    display: none;
